// ./client/src/js/main.js

// --- Importaciones Esenciales ---
// Asegúrate de que todas las importaciones necesarias estén aquí
import { initMenu } from './js/components/menuComponent.js';
import { initCardVisibility } from './js/components/cardVisibility.js';
// initMap se importa, pero su lógica interna en lazyMap.js ya usa IntersectionObserver
import { initMap } from './js/components/lazyMap.js'; 
import { initFooter } from './js/components/footerInit.js';
// Importa las funciones necesarias de animations.js
import { addHoverEffect, smoothScroll } from './js/utils/animations.js'; 
import { initWhatsApp } from './js/components/whatsapp.js';
// Importa los estilos principales
import './styles/scss/main.scss'; 

// --- Función Auxiliar para Carga Diferida (Lazy Loading) ---
/**
 * Configura un IntersectionObserver para cargar dinámicamente un módulo JS
 * e inicializar un componente cuando un elemento específico entra en el viewport.
 * @param {string} selector - El selector CSS del elemento a observar.
 * @param {function} importCallback - Una función que retorna la promesa de import() del módulo.
 * @param {string} componentName - Un nombre descriptivo del componente para los logs.
 */
function setupLazyLoad(selector, importCallback, componentName) {
    const element = document.querySelector(selector);
    
    // Si el elemento no existe en la página actual, no hacer nada.
    if (!element) {
        // console.log(`Lazy load: Elemento "${selector}" no encontrado para componente "${componentName}" (puede ser normal si no está en esta página).`);
        return; 
    }

    const observer = new IntersectionObserver((entries, observerInstance) => {
        // entries[0].isIntersecting es true si el elemento está al menos parcialmente visible
        if (entries[0].isIntersecting) {
            console.log(`%c[LazyLoad] Iniciando carga para: ${componentName}`, 'color: blue; font-weight: bold;');
            
            // Ejecuta la función que hace el import() dinámico
            importCallback()
                .then(() => {
                    console.log(`%c[LazyLoad] Módulo "${componentName}" cargado e inicializado.`, 'color: green;');
                })
                .catch(error => {
                    console.error(`%c[LazyLoad] Error cargando el módulo "${componentName}":`, 'color: red;', error);
                });

            // Una vez que se inicia la carga, deja de observar este elemento para evitar cargas múltiples.
            observerInstance.unobserve(element);
        }
    }, {
        // threshold: 0.01 // Cargar tan pronto como 1% del elemento sea visible
        // Puedes ajustar rootMargin para cargar un poco *antes* de que entre en pantalla:
        // rootMargin: '150px 0px 150px 0px' // Carga cuando esté a 150px de entrar al viewport (verticalmente)
        threshold: 0.1 // (Predeterminado) Cargar cuando el 10% es visible
    });

    // Empieza a observar el elemento
    observer.observe(element);
}

// --- Función Principal de Inicialización de la Aplicación ---
function initApp() {
    console.log('%c[InitApp] Iniciando aplicación principal...', 'color: orange; font-weight: bold;');

    // 1. Inicializa componentes críticos o que están "above the fold" (visibles al cargar)
    try {
        initMenu();
        initCardVisibility(); // Asumiendo que los .step-card están visibles pronto
        initFooter();
        initWhatsApp();
        initMap(); // ¡Importante! Ejecutar initMap para que configure SU PROPIO IntersectionObserver interno.
        console.log('[InitApp] Componentes iniciales cargados.');
    } catch (error) {
        console.error('[InitApp] Error inicializando componentes base:', error);
    }

    // 2. Añade efectos globales o listeners generales
    try {
        // Ejemplo: si tienes imágenes de certificación en varias partes
        const certImages = document.querySelectorAll('.certification-img'); 
        if (certImages.length > 0) {
            addHoverEffect(certImages);
        }
        setupSmoothScroll(); // Configura el desplazamiento suave para enlaces #
        console.log('[InitApp] Listeners globales configurados.');
    } catch (error) {
        console.error('[InitApp] Error configurando listeners globales:', error);
    }

    // --- 3. Configura la Carga Diferida para Componentes Pesados o "Below the Fold" ---
    
    // Lazy load para el componente de FAQs
    setupLazyLoad(
        '.faqs', // Selector del contenedor de la sección de FAQs
        () => import('./js/components/faq.js').then(module => {
            if (module && typeof module.initFAQ === 'function') {
                module.initFAQ(); // Llama a la función exportada
            } else {
                console.error('[LazyLoad] Error: "initFAQ" no es una función válida en faq.js');
            }
        }),
        'FAQ' // Nombre para logs
    );

    // Lazy load para el componente de Clientes
    setupLazyLoad(
        '.clients', // Selector del contenedor de la sección de clientes
        () => import('./js/components/clients.js').then(module => {
            if (module && typeof module.initClients === 'function') {
                module.initClients(); // Llama a la función exportada
            } else {
                console.error('[LazyLoad] Error: "initClients" no es una función válida en clients.js');
            }
        }),
        'Clients' // Nombre para logs
    );

    // --- FIN de la inicialización ---
    console.log('%c[InitApp] Observadores para carga diferida configurados.', 'color: orange;');
}

// --- Función Auxiliar para Configurar Smooth Scroll ---
function setupSmoothScroll() {
    document.querySelectorAll('a[href^="#"]').forEach(anchor => {
        anchor.addEventListener('click', function (e) {
            const href = this.getAttribute('href');
            // Verifica que sea un enlace interno válido (no solo '#')
            if (href && href.length > 1 && href.startsWith('#')) {
                try {
                    const targetElement = document.querySelector(href);
                    if (targetElement) {
                        e.preventDefault(); // Previene el salto brusco por defecto
                        smoothScroll(targetElement); // Usa la función importada
                    } else {
                        console.warn(`[SmoothScroll] El elemento destino "${href}" no fue encontrado.`);
                    }
                } catch (error) {
                    // Captura errores si el href no es un selector CSS válido
                    console.error(`[SmoothScroll] Selector inválido "${href}":`, error);
                }
            }
        });
    });
}


// --- Punto de Entrada: Ejecutar initApp cuando el DOM esté listo ---
// Esto asegura que todos los elementos HTML existen antes de intentar manipularlos
if (document.readyState === 'loading') {
    // Aún cargando, espera al evento DOMContentLoaded
    document.addEventListener('DOMContentLoaded', initApp);
} else {
    // El DOM ya está listo, ejecuta inmediatamente
    initApp();
}